import axios from "axios"
import TopHeader from 'components/common/topHeader'
import Search from 'components/common/search'
export default {
  data() {
    const name = window.localStorage.getItem("userName")
    return {
      name,
      friends: [], //好友列表
      // hasFriend: false,
      hasPic: false
    };
  },
  mounted() {
    this.allfriend()
    // this.findfriend()
  },
	components: {
    TopHeader,
    Search
	},
  methods: {
    allfriend() {
      axios
        .post("http://121.42.177.97:8080/red/user/allfriend.do?name=" + this.name)
        .then(res => {
          this.friends = res.data.data
          // console.log(this.friends)
        })
        .catch(res => {
          throw res
        })
    },
	/*
    findfriend() {
      axios
        .post(
          "http://121.42.177.97:8080/red/user/findfriend.do?name=" + this.name
        )
        .then(res => {
          console.log(res.data.data);
          window.localStorage.setItem("myId", res.data.data.user_id);
        })
        .catch(res => {
          throw res;
        })
    }
	*/
  }
  /*
  computed: {
    contactsInitialList () {
      return this.$store.getters.contactsInitialList
    },
    contactsList () {
      return this.$store.getters.contactsList
    }
  }
  */
}



